/*general style of all form controls*/
.sign-control {
  border-radius: 0 0.25rem 0.25rem 0 !important;
  height: 50px;
}

/* remember me custom checkbox style*/
.form-checkbox {
  position: relative;
}

.form-checkbox-label {
  margin-top: 1.5em;
  margin-left: 2.2rem;
}

.form-checkbox-label:before {
  content: '';
  position: absolute;
  left: 0;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  border: 1px solid #b2b2b2;
  -webkit-transition: -webkit-box-shadow 0.2s linear;
  transition: -webkit-box-shadow 0.2s linear;
  transition: box-shadow 0.2s linear;
  transition: box-shadow 0.2s linear, -webkit-box-shadow 0.2s linear;
}

.form-checkbox:checked ~ .form-checkbox-label:before {
  -webkit-box-shadow: 0px 0px 5px 5px #92d5eb;
  box-shadow: 0px 0px 5px 5px #92d5eb;
  border: none;
  background: url("./../../../static/correct.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 70%;
}

.checkbox-prepend {
  display: none !important;
}

/* form links style */
.form-link {
  color: #5f850c;
  text-decoration: underline;
  font-size: 1.1em;
  margin: 1em auto;
}

.form-link:hover {
  color: #99bf44;
}

@media (min-width: 768px) {
  .form-link {
    margin: auto 1em 0;
  }
}

/* form buttons style */
.form-btn {
  width: 250px;
}
